@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  /* background: #22333b; */
  background: rgb(53, 79, 82);
  background: radial-gradient(circle, #22333b 0%, #171a21 100%);
  /* background: #4d4d4d; */
}

.slide-in-left-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.slide-in-left-enter-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: block;
  justify-content: center;
  align-items: center;
}

.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #171a21;
  /* background: rgb(17, 29, 19);
  background: rgb(34, 51, 59);
  background: radial-gradient(
    circle,
    rgba(34, 51, 59, 1) 0%,
    rgba(23, 26, 33, 1) 100%
  ); */
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
}

main {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
  border-radius: 4px;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: transparent;
  border-color: #292929;
  color: #292929;
  border-radius: 4px;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: #bcb8b1;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid #bcb8b1;
    color: #bcb8b1;
    background: transparent;
  }

  .nav-links a:hover,
  .nav-links a:active,
  .nav-links a.active {
    background: #e0afa0;
    border-color: #292929;
    color: #292929;
    border-radius: 4px;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: #e0afa0;
    color: #292929;
    border-color: #292929;
  }
}

.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #22333b;
  border-radius: 4px;
  background: #e0afa0;
  color: #22333b;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #22333b;
  border-color: #22333b;
  color: #f4f3ee;
}

.button--inverse {
  background: transparent;
  color: #22333b;
}

.button--inverse:hover,
.button--inverse:active {
  color: #f4f3ee;
  background: #8a817c;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button--flip:hover span {
  display: none;
}

.button--flip:hover::before {
  content: 'Logout';
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.75rem;
  padding: 0.3rem 1rem;
}

.button--big {
  font-size: 1.5rem;
}

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 75vw;
  background: #f4f3ee;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 2.5rem;
  height: 2px;
  background: #bcb8b1;
}

.main-title {
  font-weight: 100;
  letter-spacing: 6px;
}

.main-navigation__title {
  color: white;
  flex-grow: 2;
  margin-left: 10px;
}

.main-navigation__title a {
  text-decoration: none;
  color: #bcb8b1;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
  border-radius: 4px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #22333b;
  border-color: #22333b transparent #22333b transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #f4f3ee;
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

